import React from "react";
import {Paging} from "@axa-fr/react-toolkit-table";
import diff_match_patch from 'diff-match-patch';
import JSONPretty from 'react-json-pretty';
import {totalLevenshtein} from "./score";

const filterItems = (items, filterName) => {
    return items.filter(item => {
        if (filterName === "KO"){
            return item.left.Body !== item.right.Body;
        }
        if (filterName === "OK"){
            return item.left.Body === item.right.Body;
        }
        else {
            return items;
        }
    });
}

const computeTotalTimeMs = (items) => {
    const total = {
        rightTimeMs: 0,
        leftTimeMs: 0,
    };
    items.forEach(item => {
                total.rightTimeMs = total.rightTimeMs + item.right.TimeMs;
                total.leftTimeMs = total.leftTimeMs + item.left.TimeMs;
        }
    )
    return total;
}

const orderByStatusCode = (items) => {
    const statusCodes = {};
    items.forEach(item => {
        
        if(!item.right.StatusCode || !item.left.StatusCode){
            return item;
        }
        
        if(!statusCodes[item.right.StatusCode.toString()]){
            statusCodes[item.right.StatusCode.toString()] = { right: 1, left:0 };
        } else{
            statusCodes[item.right.StatusCode.toString()].right ++;
        }

        if(!statusCodes[item.left.StatusCode.toString()]){
            statusCodes[item.left.StatusCode.toString()] = { right: 0, left:1 };
        } else{
            statusCodes[item.left.StatusCode.toString()].left ++;
        }
    }
    )
    return statusCodes;
}

const reverseString = (str) => {
    const array = Array.from(str);
    return array.reverse().join("");
}

const getExtensionName = (fileName) => {
    const reversed = reverseString(fileName);
    const start = reversed.indexOf(".") + 1;
    const end = reversed.indexOf("_");
    const extension = reversed.substr(start, end - start);
    return reverseString(extension);
}

const filterExtensions = (items, extensionName) => {
    return items.filter(item => {
        if(extensionName === "Tout"){
            return items;
        }
        if(extensionName === "JPG/JPEG"){
            return (getExtensionName(item.fileName).toUpperCase() === "JPG" || getExtensionName(item.fileName).toUpperCase() === "JPEG");
        }
        return getExtensionName(item.fileName).toUpperCase() === extensionName;
    });
}

const dmp = new diff_match_patch();

const diff = (text1, text2) => {
    dmp.Diff_Timeout = parseFloat(10);
    const d = dmp.diff_main(text1, text2);
    dmp.diff_cleanupSemantic(d);
    return dmp.diff_prettyHtml(d);
};

const filterPaging = (items, numberItems, currentPage) => {
    let pageIndex = 0;
    if (currentPage > 1) {
        pageIndex = currentPage - 1;
    }

    let controlledNumberItems = 0;
    if (numberItems > 0) {
        controlledNumberItems = numberItems;
    }

    const { length } = items;
    const beginIndex = pageIndex * controlledNumberItems;
    const lastIndex = length < beginIndex + controlledNumberItems ? length : beginIndex + controlledNumberItems;

    return items.slice(beginIndex, lastIndex);
};

const computeNumberPages = (items, numberItems) => {
    if (!items) {
        return 1;
    }
    const numberItemsControlled = numberItems < 1 ? 1 : numberItems;
    const { length } = items;
    return length > 0 ? Math.ceil(length / numberItemsControlled) : 1;
};

const StatusCode = ({statusCodes}) =>{
    const statuses =  Object.keys(statusCodes).map(function(key, index) {
        const value = statusCodes[key];
        return <div className="stats__results" key={key}>
            <div className="stats__results-div stats__results-div--border">
                <span>Status Code : {key} </span></div>
            <div className="stats__results-div stats__results-div--border">
                <span>Nombre à gauche : {value.left} </span></div>
            <div className="stats__results-div"> <span>Nombre à droite : {value.right} </span></div>
        </div>
    });
    
    return <>{statuses}</>
}

const Scores = ({scores}) =>{
    
    const keys = Object.keys(scores);
    const ScoreItems =  keys.map(function(key, index) {
        const value = scores[key];
        return <div className="stats__results" key={key}>
            <div className="stats__results-div stats__results-div--border">
                <span>Clé : {key} </span></div>
            <div className="stats__results-div stats__results-div--border">
                <ul>
                    <li>Levenshtein score : {value.score} </li>
                    <li>Number OK : {value.number_ok} </li>
                    <li>Number KO : {value.number_ko} </li>
                </ul>
            </div>
            <div className="stats__results-div"> <span>Percentage OK : {Math.round((value.number_ok / (value.number_ok + value.number_ko))*100)} % </span></div>
        </div>
    });

    return <>{ScoreItems}</>
}

const TableResult = ({state, setState}) => {

    const collapseItem = (id) => {
        const newItems = []
        state.items.map(item => {
            if (item.id === id) {
                const newItem = {...item, collapse: !item.collapse}
                newItems.push(newItem);
            } else {
                newItems.push(item)
            }
        })
        setState({...state, items: newItems});
    }

    const parseItem = (id) => {
        const newItems = []
        state.items.map(item => {
            if (item.id === id) {
                const newItem = {...item, parse: !item.parse}
                newItems.push(newItem);
            } else {
                newItems.push(item)
            }
        })
        setState({...state, items: newItems});
    }
    
    const filteredFiles = filterItems(state.items, state.filters.filterName);
    const filteredExtensions = filterExtensions(filteredFiles, state.filters.extensionName);
    const pageItems = filterPaging(filteredExtensions, state.filters.pagingSelect, state.filters.pagingCurrent);
    const totalTimeMs = computeTotalTimeMs(state.items);
    const statusCodes = orderByStatusCode(state.items);
    const levenshteinScores = totalLevenshtein(state.items, ["document_id", "document_type"]);
    
    return <>
        {filteredFiles.length === 0 ? (
            <h2 className="table-title">Il n'y a aucun fichier de type {state.filters.filterName} ou dont l'extension est {state.filters.extensionName} !</h2>
        ) : (
            <>
                <div className="stats">
                    <div className="stats__title">
                        Statistiques :
                    </div>
                    <div className="stats__results">
                        <div className="stats__results-div stats__results-div--border">
                            <span>Fichiers OK : {filterItems(state.items, "OK").length}</span></div>
                        <div className="stats__results-div stats__results-div--border">
                            <span>Fichiers KO : {filterItems(state.items, "KO").length}</span></div>
                        <div className="stats__results-div"><span>Total : {state.items.length}</span></div>
                    </div>
                    <div className="stats__results">
                        <div className="stats__results-div stats__results-div--border">
                            <span>Temps total gauche : {totalTimeMs.leftTimeMs / 1000} secondes</span></div>
                        <div className="stats__results-div stats__results-div--border">
                            <span>Temps total droite : {totalTimeMs.rightTimeMs / 1000} secondes</span></div>
                        <div className="stats__results-div"><span>Différence : {(totalTimeMs.rightTimeMs - totalTimeMs.leftTimeMs) / 1000} secondes soit +{Math.round((totalTimeMs.leftTimeMs / totalTimeMs.rightTimeMs) *100)}% de gain</span></div>
                    </div>
                    <div className="stats__results">
                        <div className="stats__results-div stats__results-div--border">
                            <span>Temps moyen gauche : {(totalTimeMs.leftTimeMs / state.items.length) / 1000} secondes</span></div>
                        <div className="stats__results-div stats__results-div--border">
                            <span>Temps moyen droite : {(totalTimeMs.rightTimeMs / state.items.length) / 1000} secondes</span></div>
                        <div className="stats__results-div"><span>Différence : {((totalTimeMs.rightTimeMs - totalTimeMs.leftTimeMs) / state.items.length) / 1000} secondes soit +{Math.round((totalTimeMs.leftTimeMs / totalTimeMs.rightTimeMs) *100)}% </span></div>
                    </div>
                    <StatusCode statusCodes={statusCodes}  />
                    <Scores scores={levenshteinScores}/>
                </div>
                
                {pageItems.map(item => (
                    <div key={item.id} className="table-result">
                        <div className="table-result__header">
                            <div>
                                <p>Résultat fichier : {item.fileName}</p>
                                <h2>{item.left.Body !== item.right.Body ? <span className="table-result__row--red">KO</span> :
                                <span className="table-result__row--green">OK</span>} Ratio temps à gauche - temps à droite
                                : {(item.right.TimeMs - item.left.TimeMs) / 1000} secondes</h2>
                            </div>
                            <div className="table-result__collapse-button" onClick={() => {collapseItem(item.id)}}>{item.collapse ? "-" : "+"}</div>
                        </div>
                        {item.collapse && (
                            <>
                                <div className="table-result__parse-button-container">
                                    <button className="table-result__parse-button" type="button" onClick={() => {parseItem(item.id)}}>{item.parse ? "Déparse" : "Parse"}</button>
                                </div>
                                <div className="table-result__column-container">
                                    <div className="table-result__column">
                                        <div className="table-margin">
                                            <div>Copier le lien vers le fichier local dans votre presse-papiers:</div>
                                            <button className="table-result__copy-clipboard-button" onClick={() => {
                                                const element = document.createElement('textarea');
                                                element.value = item.left.FileDirectory
                                                document.body.appendChild(element)
                                                element.select()
                                                document.execCommand("copy")
                                                document.body.removeChild(element)
                                            }}>
                                                Cliquez ici
                                            </button>
                                            <h3>{item.left.Url}</h3>
                                            <div className="table-result__column--time-span">Time: {item.left.TimeMs / 1000} seconds</div>
                                            <div>{item.parse ?<JSONPretty id="json-pretty" data={item.left.Body}/>: item.left.Body}</div>
                                        </div>
                                    </div>
                                    <div className="table-result__column table-result__column--border-left">
                                        <div className="table-margin">
                                            <div>Copier le lien vers le fichier local dans votre presse-papiers:</div>
                                            <button className="table-result__copy-clipboard-button" onClick={() => {
                                                const element = document.createElement('textarea');
                                                element.value = item.right.FileDirectory
                                                document.body.appendChild(element)
                                                element.select()
                                                document.execCommand("copy")
                                                document.body.removeChild(element)
                                            }}>
                                                Cliquez ici
                                            </button>
                                            <h3>{item.right.Url}</h3>
                                            <div className="table-result__column--time-span">
                                                Time: {item.right.TimeMs / 1000} seconds
                                            </div>
                                        <div>{item.parse ?<JSONPretty id="json-pretty" data={item.right.Body}/>: item.right.Body}</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="table-result__row table-result__row--overflow"
                                dangerouslySetInnerHTML={{__html: diff(item.left.Body, item.right.Body)}}
                                />
                            </>
                        )}

                    </div>
                ))}
                
                <Paging
                    currentPage={state.filters.pagingCurrent}
                    numberPages={computeNumberPages(filteredExtensions, state.filters.pagingSelect)}
                    numberItems={state.filters.pagingSelect}
                    id="paging"
                    onChange={(e) => {
                        setState({...state,
                            filters: {
                            ...state.filters,
                                pagingSelect: e.numberItems,
                                pagingCurrent: state.filters.pagingCurrent > computeNumberPages(filteredExtensions, e.numberItems) ? 1 : e.page
                            }
                        });
                    }}
                />
            </>
        )}
    </>;
}

export default TableResult;
