import React from "react";
import {File} from "@axa-fr/react-toolkit-form-input-file";
import {SelectBase} from "@axa-fr/react-toolkit-form-input-select";
import cuid from "cuid";
import {stringify} from "json-stable-stringify";
import AceEditor from "react-ace";

import "ace-builds/src-noconflict/mode-javascript";
import "ace-builds/src-noconflict/theme-github";

const optionsSelect= [
    {value: 'KO', label: "KO"},
    {value : 'OK', label: "OK"},
    {value : 'Tout', label: 'Tout'}
];

const extensionsSelect = [
    {value: 'Tout', label: 'Tout'},
    {value: 'PNG', label: 'PNG'},
    {value: 'JPG/JPEG', label: 'JPG/JPEG'},
    {value: 'PDF', label: 'PDF'},
    {value: 'TIFF', label: 'TIFF'}
];

const FileTreatment = ({state, setState}) => {

    const tryFormatOutput = (data) => {
        try {
            const jsonString = JSON.parse(data);
            return stringify(jsonString);
        } catch (e){
            return data;
        }
    };

const mapItems = (data) => (data.map(item => {
    const left = tryFormatOutput(item.Left.Body);
    const right = tryFormatOutput(item.Right.Body);
    return {
        fileName: item.FileName,
        left: { Body: left, TimeMs: item.Left.TimeMs, Url: item.Left.Url, FileDirectory: item.Left.FileDirectory, StatusCode: item.Left.StatusCode},
        right: { Body: right, TimeMs: item.Right.TimeMs, Url: item.Right.Url, FileDirectory: item.Right.FileDirectory, StatusCode: item.Right.StatusCode },
        id : cuid(),
        parse: false,
        collapse: true,
    }
}));

const formatBody = (bodyJson, side) => {
    let rowBodyInput = bodyJson;
    let rowBodyOutput = rowBodyInput
    eval(side);
    return rowBodyOutput;
}

const formatJson = (items) => {
    if (!state.filterLeft && !state.filterRight) {
        return items;
    } else {
        try {
            const newItems = items.map(item => {
                const newItem = item;
                if (state.filterLeft) {
                    newItem.left.Body = formatBody(item.left.Body, state.filterLeft);
                }
                if (state.filterRight) {
                    newItem.right.Body = formatBody(item.right.Body, state.filterRight);
                }
                return newItem;
            });
            return newItems;
        } catch (error) {
            console.log(error);
            return items
        }
    }
};

    const loadFile = e => {
        const input = e.values[0].file;
        const reader = new FileReader();
        reader.onloadend = () => {
            const result = JSON.parse(reader.result);
            setState({...state, items: formatJson(mapItems(result))});
        }
        reader.readAsText(input);
    };

    return(
        <>
            <div className="file-input">
                <File
                    id="file_loader"
                    name="placeJsonFile"
                    accept="application/*, text/plain"
                    onChange={loadFile}
                />
            </div>

            <div className="select">
                <div className="select__status-filter-container">
                    <p className="select__title">État de fichier :</p>
                    <SelectBase
                    id="select_type"
                    name="SelectType"
                    value={state.filters.filterName}
                    options={optionsSelect}
                    onChange={(e) => {
                        setState({...state,
                            filters: {
                            ...state.filters,
                                filterName:e.value,
                                pagingCurrent: 1
                            }
                        });
                    }}
                />
                <p className="select__title">Extensions de fichiers:</p>
                <SelectBase
                    id="extension_type"
                    name="ExtensionType"
                    value={state.filters.extensionName}
                    options={extensionsSelect}
                    onChange={(e) => {
                        setState({...state,
                            filters: {
                                ...state.filters,
                                extensionName:e.value,
                                pagingCurrent: 1
                            }
                        });
                    }}
                />
                </div>
                <div className="select__function-filter">
                    <label>filtre gauche</label>
                    <AceEditor
                        mode="javascript"
                        theme="github"
                        onChange={e => setState({...state, filterLeft: e})}
                        value={state.filterLeft}
                        name="AceEditorLeft"
                        editorProps={{ $blockScrolling: true }}
                    />
                </div>
                <div className="select__function-filter">
                    <label>filtre droite</label>
                    <AceEditor
                        mode="javascript"
                        theme="github"
                        onChange={e => setState({...state, filterRight: e})}
                        value={state.filterRight}
                        name="AceEditorRight"
                        editorProps={{ $blockScrolling: true }}
                    />
                </div>
            </div>
        </>
    )
};

export default FileTreatment;