import React, { useState} from "react";
import {Header, Name, Title} from "@axa-fr/react-toolkit-layout-header";
import TableResult from "./TableResult";
import FileTreatment from "./FileTreatment";
import logo from '@axa-fr/react-toolkit-core/dist/assets/logo-axa.svg';
import './HandleFile.scss';

const HandleFile = () =>{

    const [state, setState] = useState({
        fileName: "Comparer un fichier JSON",
        items:[],
        filters: {
            filterName: "KO",
            extensionName: "Tout",
            pagingSelect: 50,
            pagingCurrent: 1
        },
        filterRight: `
try { 
    let body = JSON.parse(rawBodyInput);
    rawBodyOutput = JSON.stringify(body);
} catch(ex) {
    console.log("Plantage parsing right");
    console.log(ex.toString());
    rawBodyOutput = rawBodyInput;
}`,
        filterLeft: `
try { 
    let body = JSON.parse(rawBodyInput);
    rawBodyOutput = JSON.stringify(body);
} catch(ex) {
    console.log("Plantage parsing right");
    console.log(ex.toString());
    rawBodyOutput = rawBodyInput;
}`
    });

    return (
        <>
            <Header>
                <Name
                    title="ML-CLI"
                    subtitle="Made by AXA"
                    img={logo}
                    alt="Logo AXA"
                />
            </Header>
            <Title title={state.fileName === "Comparer un fichier JSON" ? state.fileName: "Fichier en cours de visualisation : " + state.fileName}/>
            <FileTreatment state={state} setState={setState} />
            {state.items.length === 0 ? "" : <TableResult state={state} setState={setState} /> }
        </>

    );
}

export default HandleFile;
